.app-header {
  display: flex;
  justify-content: space-between;
  padding-top: 72px;
  width: 103%;
  transform: translateX(-3%);
}

.contact-btn {
  width: 153px;
  height: 48px;
  border: 2px solid #fff;
  background-color: transparent;
  border-radius: 2vw;
  font-family: "livvic";
  color: #fff;
  cursor: pointer;
}

.nav-links {
    display: flex;
    justify-content: space-between;
    width: 380px;
}

.header-links {
    color:#fff;
    display: flex;
    font-size: 18px;
    justify-content: space-between;
    width: 140px;
}

.header-links {
    cursor: pointer;
}